import { FaShoppingCart } from 'react-icons/fa';  // Shopping cart icon from FontAwesome
import { FiSearch } from 'react-icons/fi'; // Search icon from Feather Icons

export default function Header() {
  return (
    <header className="bg-white shadow-md sticky top-0 z-50">
      <div className="container mx-auto flex items-center justify-between p-4">
        <div className="text-lg font-bold text-gray-800">Ecommerce</div>
        <div className="flex items-center space-x-2">
          <input
            type="text"
            placeholder="Search products..."
            className="border border-gray-300 rounded-md px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          <button className="p-2 text-gray-700">
            <FiSearch size={20} />
          </button>
        </div>
        <nav className="space-x-4">
          <button className="flex items-center">
            <FaShoppingCart size={20} />
            <span className="ml-2">Cart</span>
          </button>
        </nav>
      </div>
    </header>
  );
}
